import React from 'react'
import personService from './Services/person'



const Person = ({person, persons, setPersons, setSucessMessage}) =>{
    const id = person.id
    const handleDelete = () =>{
        if(window.confirm(`Are you sure you want to delete ${person.name}?`)){
            personService
            .remove(id)
            .then(response =>{
                setSucessMessage(`${person.name} has been succesfully deleted.`)

                setTimeout(() => {
                    setSucessMessage(null)
                }, 4000);
            })

        
        const deletedPeople = persons.filter(person => person.id !== id)
        setPersons(deletedPeople)
        }
    }

    return (
        <>
            {person.name} {person.number} <button className={"delete-button"} onClick={handleDelete}>Delete</button>
        </>
    )
}

export default Person