import React, { useEffect, useState } from "react";
import PersonsForm from "./PersonsForm";
import Persons from "./Persons";
import Filter from './GoodFilter';
import ChangePerson from './ChangePerson'
import personService from './Services/person.js'
import './index.css'
import Notification from "./Notifcation";
import SucessMessage from "./SuccessMessage";

 
const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("Enter name");
  const [newNumber, setNewNumber] = useState("Enter number-number");
  const [ filter , setFilter ] = useState('')
  const [checked, setChecked] = useState(false)
  const [change , setChange] = useState('')
  const [changeNumber, setChangeNumber] = useState('')
  const [errorMessage, setErrorMessage] = useState(null)
  const [sucessMessage, setSucessMessage] = useState(null)
  useEffect(() =>{
    personService
      .getAll()
      .then(response =>{
        setPersons(response.data)
      })
  }, [])
  
  const addPerson = (event) => {
    event.preventDefault();
    const newPersons = persons.concat({ name: newName, number: newNumber });
    for (let i = 0; i < persons.length; i++) {
      if (persons[i].name === newName || persons[i].number === newNumber) {
        if (persons[i].name === newName) {
          if(window.confirm(`${persons[i].name} is alredy in the numberbook, would you like to replace his number number?`)){
            personService
              .update(persons[i].id, {name: newName, number: newNumber})
              .catch(error =>{
                setErrorMessage(`${persons[i].name} was already removed or is non existent.`)
                setTimeout(() => {
                  setErrorMessage(null)
                }, 5000);
              })
              .then(response =>{
                setPersons(persons.map((person) => (person.id !== persons[i].id ? person : response.data)))
                setTimeout(() => {
                  window.alert(`${newName}'s number number has been updated to ${newNumber}.`)
                }, 500);
                
                setNewName("")
                setNewNumber("")
                
              })
            
          }
        } 

        else if (persons[i].number === newNumber) {
          window.alert("This number number already belongs to somebody else");
        } 
        return
      }
    }
    personService
      .create({ name: newName, number: newNumber })
      .then(response => {
        setPersons(persons.concat(response.data))
        setNewName("");
        setSucessMessage(`${newName} has been added.`)
        setTimeout(() => {
          setSucessMessage(null)
        }, 4000);
        
      })
    setPersons(newPersons);
    setNewNumber("");
    setNewNumber("")
  };
  
  const handleNameChange = (event) =>{
    event.preventDefault()
      if(checked === true){
      window.alert("The filter must be turned off, try again.")
      return
  }
  
  let changedContact = {}
  let personsChanged = [...persons]
  let x = true
    for( let i = 0; i < persons.length; i++){
    if(persons[i].number === changeNumber && change !== persons[i].name){
      window.alert("This number number belongs to another person, " + persons[i].name)
      x = false
      return
    }
  } 
  if(x){
    for( let i = 0; i < persons.length; i++){
      if(persons[i].name === change && persons[i].number !== changeNumber){
        changedContact = {
          name: change,
          number: changeNumber
        }
        x = i
        personsChanged[i] = changedContact
        personService
          .update(persons[i].id, changedContact)
          .then(response =>{
            setPersons(persons.map((person) => (person.id !== persons[i].id ? person : response.data)))
            setTimeout(() => {
              window.alert(`${change}'s number has been changed to ${changeNumber}`)
            }, 500);
            setChange("")
            setChangeNumber("")
          })
          .catch(error=>{
            setErrorMessage(`${change} has been removed from server in another page. Please refresh.`)

            setTimeout(() => {
              setErrorMessage(null)
            }, 4000);
            
          })
        
          
        
        return
      }
      else if(persons[i].number === changeNumber && change !== persons[i].name){
        x = i
        window.alert("This number number belongs to another person, " + persons[i].name)
        return
      }
      x = i
    }
  let alert = change + " isn't in numberbook, or the number is the same."
  if(x !== true && x !== false){
    alert =
      persons[x].name === change
      ? "The number number is the same"
      : change + " isn't in the numberbook"
      
  }
    // window.alert(alert)
    setErrorMessage(alert)
    setTimeout(() => {
      setErrorMessage(null)
    }, 4000);
  }
  }
  
  
  let showedPeople =
  filter === "" || checked === false
  ? persons
  : persons.filter(person => person.name.includes(filter))
  const handleChange = (event) => {
    setNewName(event.target.value);
  };

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value);
  };
  return (
    <div>
      <Filter checked={checked} setChecked={setChecked} filter={filter} setFilter={setFilter}/>
      <Notification message={errorMessage} />
      <SucessMessage sucess={sucessMessage} />
      <PersonsForm addPerson={addPerson}  handleChange={handleChange} newName={newName} newNumber={newNumber} handleNumberChange={handleNumberChange}/>
      <Persons setSucessMessage={setSucessMessage} persons={persons} setPersons={setPersons} showPeople={showedPeople}/>
      <ChangePerson changeNumbner={changeNumber} setChangeNumber={setChangeNumber} handleNameChange={handleNameChange} change={change} setChange={setChange}/>
    </div>
  );
};
 
export default App
