import React from "react";
 
const ChangePerson = ({
 change,
 setChange,
 handleNameChange,
 changeNumber,
 setChangeNumber
}) => {
 const handleChange = (event) => {
   setChange(event.target.value);
 };
 
 const handleNumberChange = (event) => {
   setChangeNumber(event.target.value);
 };
 
 return (
   <div>
     <h2 className={"change-title"}>Change/Edit Contact</h2>
     <form onSubmit={handleNameChange}>
       Person to Change: <input value={change} onChange={handleChange} />
       <br />
       New Number: <input value={changeNumber} onChange={handleNumberChange} />
       <input className={"form-buttons"} type={"submit"} value={"Submit"} />
     </form>
   </div>
 );
};
 
export default ChangePerson;
