import React from "react";
const Filter = ({filter, setFilter, checked, setChecked}) =>{
  
  
   const filterHandler = (event) =>{
       setFilter(event.target.value)
   }
   const handleCheckbox = (event) =>{
       setChecked(event.target.checked)
      
      
   }
   return(
       <div>
           <h2 className={"title"}>Phonebook</h2>
           <h2>Filter</h2>
           <form>
               Filter (name): <input value={filter} onChange={filterHandler}></input>
               <br/>
               <label htmlFor={"showFilter"}>Activate filter</label>
               <input id={"showFilter"} value={checked} type={"checkbox"} onChange={handleCheckbox}/>
           </form>
       </div>
   )
}
export default Filter
