import React from "react";
import "./index.css"
 
const PersonsForm = ({
 addPerson,
 handleChange,
 newName,
 newNumber,
 handleNumberChange
}) => {
 return (
   <div>
     <h2>Add Person</h2>
     <form onSubmit={addPerson} className={"personsForm"}>
       <div>
         name: <input onChange={handleChange} placeholder={newName} />
         <div>
           number:{" "}
           <input placeholder={newNumber} onChange={handleNumberChange} />
         </div>
       </div>
       <div>
         <button className={"form-buttons"} type="submit">Add</button>
       </div>
     </form>
   </div>
 );
};
 
export default PersonsForm;
