import React from 'react'
import Person from './Person'

const Persons = ({showPeople, persons, setPersons, sucess, setSucessMessage}) =>{

    return(
        <div>
            {showPeople.map((person, i) => (
                <p key={i}>
                    <Person setSucessMessage={setSucessMessage} persons={persons} setPersons={setPersons} person={person} showPeople={showPeople}/>
                    {/* {person.name} {person.number} */}
                </p> 
            ))}
        </div>
    )
}


export default Persons